.card {
  width:35%;
  margin-left: 4%;
  margin-top: 30px;
  padding: 1rem;
  border-radius: 14px;
  background-color: rgb(242, 242, 242);
  display: inline-block;
}

.card:hover{
  box-shadow: 4px 4px 8px 8px rgba(242, 242, 242);
}

@media (max-width:450px) {
  .card {
    width:45%;
  }
}